import apiProxyHelper from '../helpers/api.proxy.helper'

export class SettingsService {

    constructor() {

    }

    /**   Config  */
    config_read(args) {
        console.log(args.sessionId);
        const $result = apiProxyHelper.apiProxy({ url: `config?sessionId=${args.sessionId}`, method: 'GET' }, );
        return $result;
    }

    /**  Config */
    config_modify(args) {
        console.log(args.sessionId);
        const modifyData = {
            sessionId: args.sessionId,
            data: args.body
        }
        const $result = apiProxyHelper.apiProxy({ url: `config`, body: modifyData, method: 'POST' });
        return $result;
    }

    /**  */
    floor_create_new_floor(args) {
        console.log(args);
        const createData = {
            sessionId: args.sessionId,
            floor: args.floor,
            name: args.name,
            image: args.image
        }
        const $result = apiProxyHelper.apiProxy({ url: `floors`, body: createData, method: 'POST' }, );
        return $result;
    }

    /**  */
    floor_delete_floor(args) {
        console.log(args);

        const $result = apiProxyHelper.apiProxy({ url: `floors?sessionId=${args.sessionId}&objectId=${args.objectId}`, method: 'DELETE' }, );
        return $result;
    }

    /**  */
    floor_get_all_floors(args) {
        console.log(args.sessionId);
        const $result = apiProxyHelper.apiProxy({ url: `floors?sessionId=${args.sessionId}`, method: 'GET' }, );
        return $result;
    }

    /**  */
    floor_modify_floors(args) {
        console.log(args);
        const modifyData = {
            sessionId: args.sessionId,
            objectId: args.objectId,
            name: args.name,
            image: args.image
        }
        const $result = apiProxyHelper.apiProxy({ url: `floors`, body: modifyData, method: 'PUT' }, );
        return $result;
    }

    /**  */
    camera_create_camera(args) {
        const createData = {
            sessionId: args.sessionId,
            floor: args.floor,
            name: args.name,
            sourceid: args.sourceid,
            x: args.x,
            y: args.y,
            angle: args.angle
        }
        const $result = apiProxyHelper.apiProxy({ url: `cameras`, body: createData, method: 'POST' }, );
        return $result;
    }

    /**  */
    camera_delete_camera(args) {
        console.log(args);

        const $result = apiProxyHelper.apiProxy({ url: `cameras?sessionId=${args.sessionId}&objectId=${args.objectId}`, method: 'DELETE' }, );
        return $result;
    }

    /**  */
    camera_get_all_cameras() {

    }

    /**  */
    camera_modify_camera(args) {
        console.log(args);
        const modifyData = {
            sessionId: args.sessionId,
            objectId: args.objectId,
            floor: args.floor,
            name: args.name,
            sourceid: args.sourceid,
            x: args.x,
            y: args.y,
            angle: args.angle,
        }
        const $result = apiProxyHelper.apiProxy({ url: `cameras`, body: modifyData, method: 'PUT' }, );
        return $result;
    }



}


export default new SettingsService();


